import React, { PureComponent } from 'react';
import ChaChing from '../assets/ChaChing.JPG';
import Button2 from '../components/button2';

export default function project(props) {

    if (props.tools) {
        var list = props.tools.map(function (tool) {
            return (
                <ul >
                   <li>{tool}</li>
                </ul>
           
            );
        });
    }

    return (

        <div className="background-project">
            <div class="flip-card">
                <div class="flip-card-inner">
                    <div class="flip-card-front">
                        <div className="indent"></div>
                        <div className="project-desc">
                            <div className="title">
                                {props.title}
                                <span className="place">{props.place}</span>
                            </div>
                        </div>
                        <div className="project-img-box">
                            <img src={props.img} alt="" class="proj-img" />
                        </div>
                    </div>
                    <div class="flip-card-back">
                        <div className="proj-about">
                            <div className="list">
                                <h1>Tools</h1>
                                    {list}
                            </div>
                            <div className="proj-about-right">
                            {props.about1}
                             <br /><br />
                            {props.about2}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div >
    )

}
