import React, { Component } from 'react'
import Cheerleading from "../assets/Cheerleading.jpg";
import Hiking from "../assets/Hiking.jpg";
import Reading from "../assets/Reading.jpg";
import Crochet from "../assets/Crochet.jpg";
import Picnic from "../assets/Picnic.jpg";
import Coding from "../assets/Coding.jpg";
import Socials from "../components/socials";
import Headshot2 from "../assets/Headshot2.JPG";

export default class about extends Component {
    constructor() {
        super();
        this.state = { text : '' }
      }
  
  
      handleChange = param => e => {
          this.setState({text: param})
        
      }
  
      onMouseout (e) {
          this.setState({text : ''})
        }
  
    
    render() {
        return (
            <div className="about-box" id="about-page">
                <div className="about-img-page">
                    <div class="img-description">I am  ...
                        <span className="description">  {this.state.text}</span></div>
                    <div className="img-box">
                        <img class="img-about" src={Cheerleading} alt=""onMouseEnter={this.handleChange("A Western Cheerleader")} onMouseLeave={this.onMouseout.bind(this)}/>
                        <img class="img-about" src={Hiking} alt="" onMouseEnter={this.handleChange("An Outdoors Adventurer")} onMouseLeave={this.onMouseout.bind(this)} />
                        <img class="img-about" src={Reading} alt="" onMouseEnter={this.handleChange("A Book Worm")} onMouseLeave={this.onMouseout.bind(this)}/>
                        <img class="img-about" src={Crochet} alt="" onMouseEnter={this.handleChange("An Artsy Crocheter")} onMouseLeave={this.onMouseout.bind(this)} />
                        <img class="img-about" src={Picnic} alt="" onMouseEnter={this.handleChange("An Picnic Enthusiat")} onMouseLeave={this.onMouseout.bind(this)}  />
                        <img class="img-about" src={Coding} alt="" onMouseEnter={this.handleChange("An Eager Programmer")} onMouseLeave={this.onMouseout.bind(this)} />
                    </div>
                </div>
                <div className="about-page">
                    <div className="about-left">
                        <div className="about-text">
                            <span>TURNING IDEAS INTO REALITY</span>
                            <br />
                            <br />
                            I am a fourth year Software Engineering Student at Western University. I am currently
                            completing an internship year at Geotab as a Software Developer Intern.
                            <br /><br /> 
                            I enjoy full stack development and love paying attention to all the details.
                            I am driven to continously improve and I take pride in delivering clean and working solutions.
                            <br /><br />
                            I appreciate learning and collaborating with mentors and peers. So please feel free to reach out to me!
                            I am eager to continue learning new languages and technologies and I am excited to continue my 
                            development through my academics and extracurriculars.
                        </div>
                    </div>
                    <div className="about-right">
                        <div className="about-background">
                            <div className="img-about-background">
                                <img src={Headshot2} alt="" class="about-headshot" />
                            </div>
                        </div>
                        <div className="socials-box">
                        <Socials></Socials>
                        </div>
                       
                    </div>
                </div>
            </div>
        )
    }
}
