import React, { PureComponent } from 'react';
import { Link, animateScroll as scroll } from "react-scroll";


export default class navigation extends PureComponent {
    scrollToTop = () => {
        scroll.scrollToTop();
      };
    

    render() {
        return (
            <nav class="sidebar">
                <div class='fixed'>
                <ul class="side-nav">
                    <li class="side-nav__item">
                        <Link className='side-nav__link'
                          activeClass="active"
                          to="home-page"
                          spy={true}
                          smooth={true}
                          offset={-50}
                          duration={500}>
                            <span>Home</span>
                        </Link>
                    </li>
                    <li class="side-nav__item">
                        <Link className='side-nav__link'
                          activeClass="active"
                          to="about-page"
                          spy={true}
                          smooth={true}
                          offset={-50}
                          duration={500}>
                            <span>About Me</span>
                        </Link>
                    </li>
                    <li class="side-nav__item">
                        <Link className='side-nav__link'
                          activeClass="active"
                          to="project-page"
                          spy={true}
                          smooth={true}
                          offset={-50}
                          duration={500}>
                            <span>Projects</span>
                        </Link>
                    </li>
                    <li class="side-nav__item">
                        <Link className='side-nav__link'
                          activeClass="active"
                          to="resume-page"
                          spy={true}
                          smooth={true}
                          offset={-50}
                          duration={500}>
                            <span>Resume</span>
                        </Link>
                    </li>
                </ul>
                <div class="chat">
                   Lets Chat! <br></br> stan229@uwo.ca <br /> 613-888-3099
                </div>
                </div>
            </nav>


        )
    }
}
